import React from 'react';
import { NavLink } from 'react-router-dom';

import './NavLinks.css';

const NavLinks = props => {
    return(
    <ul className='nav-links'>
        <li>
            <NavLink to='./' exact>
                All Users
            </NavLink>
        </li>
        <li>
            <NavLinks to='/u1/places'>
                My Places
            </NavLinks>
        </li>
        <li>
            <NavLinks to='/places/new'>
                Add place
            </NavLinks>
        </li>
        <li>
        <NavLinks to='/auth'>
            Authenticate
        </NavLinks>
        </li>
    </ul>
    )
};

export default NavLinks;